import React from "react";
import { Carousel } from "react-bootstrap";
import { useSelector } from "react-redux";
import * as h from "~/Helpers";

const Lists = () => {
   const { module } = useSelector((e) => e.redux);
   const { listContent } = module;

   const carouselSettings = {
      slide: true,
      fade: true,
      controls: false,
      indicators: false,
      interval: 100,
      keyboard: true,
   };

   return (
      h.arrLength(listContent) && (
         <Carousel {...carouselSettings}>
            {listContent.map((row) => {
               return (
                  <Carousel.Item key={h.parse("id", row)}>
                     <img
                        className="d-block w-100"
                        src={`http://localhost:8080/getfile/images/${h.parse("filename", row)}`}
                        alt="First slide"
                        style={{ height: innerHeight, width: "auto !important" }}
                     />
                  </Carousel.Item>
               );
            })}
         </Carousel>
      )
   );
};
export default Lists;
